import { Component, OnInit } from '@angular/core';
import { ArticoliService } from '../services/articoli.service'

@Component({
    selector: 'app-articoli',
    templateUrl: './articoli.component.html'
})
export class ArticoliComponent implements OnInit {

    title: string = 'Articoli';
    textValue = '';
    
    get articoli(): any[]  {
        let list=this.articoliService.getArticoli();
        if(this.textValue) {
            let str=this.textValue.toLowerCase();
            list=list.filter(a=>a.codice.toLowerCase().includes(str) || a.descrizione.toLowerCase().includes(str) );
        }
        return list;
    }

    constructor(private articoliService:ArticoliService) { }

    ngOnInit() {

    }
}
