import { Injectable } from '@angular/core'
import { HttpClient } from '@angular/common/http';
import { HttpErrorResponse } from '@angular/common/http';
import { Orario } from './orario'

@Injectable()
export class OrariService {

    startDate = new Date();
    defaultOrario = '15:00-18:00'
    dateApertura = [];
    dateChiusura: Orario[] =[];
  

    constructor(private httpService: HttpClient) {

        this.httpService.get('./assets/orari.json').subscribe(
            data => {
                this.dateChiusura = data as Orario[];	 // FILL THE ARRAY WITH DATA.
                this.buildOrari();
                console.log(this.dateApertura);
                console.log(this.dateChiusura);
            },
            (err: HttpErrorResponse) => {
                console.log(err.message);
            }
        );
    }

    private buildOrari() {
        //trova i prossimo sabato a partire da oggi
        while (this.startDate.getDay() != 6) {
            this.startDate.setDate(this.startDate.getDate() + 1);
        }

        //crea l'elenco dei prossimi 4 sabati
        while (this.dateApertura.length < 4) {

            let data = new Date(this.startDate);
            let orario = this.defaultOrario;
            let style = ''
            // trova correzioni all'orario standard
            let obj = this.dateChiusura.find(o => o.data === data.toISOString().substring(0,10) );
            if (obj) {
                orario = obj.orario;
                if (orario !== this.defaultOrario) {
                    style = 'bg-warning';
                } 
            }
            this.dateApertura.push({ data: data, orario: orario, style: style })

            this.startDate.setDate(this.startDate.getDate() + 7);
        }
    }

    

    getOrari(): any[] {
        console.log(JSON.stringify(this.dateApertura))
        return this.dateApertura;
    }

}